nav {
  background: white;
  max-height: 80%;
  overflow-y: auto;
  overflow-x: hidden;
  position: fixed;


 a {
    padding: $inline-padding;
    &.active {
      background: $pillar-color;
      color: white;
      font-weight: bold;
    }
    &.crumb {
      background: $god-blushed;
      font-weight: bold;
    }
    &.active + nav,
    &.crumb + nav {
      background: $god-blushed;
      margin: 0;
      > a {
        padding-left: $personal-space;
      }
    }

 }
 @include tight_and_cosy {
   top: 0px;
 }
}
