// LAYOUT ==================
$god-space: 1%; // the horizontal padding in all the prongs.
$heaven-width: 3%; // prong width right (as a percentage to help scaling).
$hell-width: 27%; // prong width left (as a percentage to help scaling).
$personal-space: 9px; // the vertical padding between believers.
$transition: 0.5s; // MEDIA BREAKS ==================
$god-loose: 1200px; // loose - prongs divided - no overlap.
$god-cosy: 1024px; // cosy - overlap between sinners and the pillar.
$god-tight: 768px; // tight - believers full width - piller width small margin.
$heaven-cosy: 0%; // cosy - allow the heaven width to grow by this amount.
$hell-cosy: 10%; // cosy - allow the hell width to grow by this amount.
$pillar-cosy: 0%; // cosy - allow the pillar width to grow by this amount.
// COLORS ==================
$heaven-color: black; // dark color: mainly for bg with $pillar-color text.
$pillar-color: #451F11; // dark color: mainly for heading text on white bg.
$hell-color: black; // dark color: mainly for bg with $pillar-color text.
$god-color: black; // $god-color: the primary color
$god-blushed: lighten($pillar-color, 75); // $god-color: the primary tint color
// TEXT ==================
$god-h1: 150%; // font size of h1.
$god-h6: 80%; // font size of h6.
$god-p: 80%; // font size of paragraph text.
$god-spoke: 100%; // standout with big text.
$god-head: sans-serif; // for heading level text.
$god-body: sans-serif; // for body level text.
// BASICS ==================
$god-border: 1px $pillar-color solid; // general purpose border
$inline-padding: 3px;
$toothpaste-color: 'none'; // BELIEVERS ==================
$container: (body, main); // sin classes only affect direct children of these
$pillar: (
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  ul,
  ol,
  dl,
  figure,
  form,
  footer,
  table,
  section,
); // pillar - all tags are stictly contained the same way - no believers here.
$heaven: (blockquote); // hangs right - gets the best views.
$hell: (nav); // hangs left.
