section {
  flex: initial;
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;

  > figure {
    @include colorize_god($god-blushed, $god-color, $god-border);
    margin: $inline-padding;
    box-sizing: border-box;
    width: 18%;
    @include cosy {
      width: 48%;
    }
    @include tight {
      width: 100%;
    }
    img {
      height: auto;
        width: 100%;
    }
  }

  > article {
    @include colorize_god($god-blushed, $god-color, $god-border);
    margin: $personal-space;
    padding: $personal-space;
    display: grid;
    grid-template-columns: 100px auto;
    grid-template-rows: 30px auto;
    grid-template-areas: "article-a article-h4" "article-a article-div";
    width: 100%;

    > a {
      display: block;
      grid-area: article-a;
      padding: $inline-padding;

      img {
        width: 100%;
      }
    }

    > h4 {
      grid-area: article-h4;
      margin: 0;
      padding: $inline-padding;
    }

    > div {
      grid-area: article-div;
      margin: 0;
      padding: $inline-padding;
    }
  }
}
